import { mapGetters } from "vuex";
export default {
  methods: {
    async logout() {
      await this.$auth.logout(); // this method will logout the user and make token to false on the local storage of the user browser
    },
  },
  computed: {
    ...mapGetters(["getUserInfo", "isAuthenticated"]),
  },
};
